import React,{Fragment,useState,useEffect} from "react";
import {Card, Row} from "react-bootstrap";
import axios from "axios";
import Switch from '@material-ui/core/Switch';
import {useParams} from "react-router-dom";
import {useSelector} from "react-redux";
import style from "./Edit.module.css";


export default function Edit (){
    const {id} = useParams();
    const allEmployeeFromReducers = useSelector(state => state.allMembers);
    console.log(allEmployeeFromReducers);
    
    const member = allEmployeeFromReducers.find((ele) => ele.memberId == id)
    useEffect(()=>{
    setData(member)
    },[id])
   
    const [data, setData] = useState({
        firstName: "",
        lastName : "", 
        specification: "",
        address: "",
        kpi : 0,
        supervisor :'',
        generatedId: 0,
        duration: 0,
        startDate: "",
        endDate: "",
        projects:[],
        hardSkills: [],
        softSkills:[],
        isEmployee :Boolean(),
        isIntern: Boolean(),
      



    });
    const handelSubmet = (e)=>{
        const {name , value} = e.target;
        if (name === "projects" || name ==="softSkills" || name === "hardSkills") {
            setData((prevstate) => ({...prevstate,[name]: value}))
        
        }
    }
    
    const edit = async() =>{
        await axios.put('https://immense-shelf-33121.herokuapp.com/v1/users',{
            memberId: data.memberId,
            generatedId : data.generatedId,
            firstName : data.firstName,
            lastName : data.lastName,
            specification :data.specification,
            address : data.address,
            kpi : data.kpi,
            supervisor : data.supervisor,
            projects : data.projects.split(','),
            hardSkills : data.hardSkills.split(','),
            softSkills: data.softSkills.split(','),
            isEmployee : data.isEmployee,
            isIntern : data.isIntern,
            duration : data.duration,
            startDate : data.startDate,
            endDate : data.endDate,
        }).then((res)=>{
            console.log(res);
        }).catch((err)=>{
            console.log(err);
        })
    }
    return(
        <>
       <Row className="m-0">
          <div className="col-md-8 col-sm-12 mb-5">
              <Card className={style.Card}>
                  <div className={style.edit}>
                      <Fragment>
                          <Row>
                          <label>firstName</label>
                          <input
                                  style={{display:'block'}}
                                  name="firstName"
                                  value={data.firstName}
                                  type="text"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                              <label>lastName</label>
                              <input
                                  style={{display:'block'}}
                                  name="lastName"
                                  value={data.lastName}
                                  type="text"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                                  <label>specification</label>
                                  <input
                                  style={{display:'block'}}
                                  name="specification"
                                  value={data.specification}
                                  type="text"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                                  <label>address</label>
                                  <input
                                  style={{display:'block'}}
                                  name="address"
                                  value={data.address}
                                  type="text"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                              />
                                  <label>kpi</label>
                                  <input
                                  style={{display:'block'}}
                                  name="kpi"
                                  value={data.kpi}
                                  type="number"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                              />
                                   <label>supervisor</label>
                                  <input
                                  style={{display:'block'}}
                                  name="supervisor"
                                  value={data.supervisor}
                                  type="text"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                                  <label>duration</label>
                                  <input
                                  style={{display:'block'}}
                                  name="duration"
                                  value={data.duration}
                                  type="number"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                              />
                                  
                                  <label>generatedId</label>
                                  <input
                                  style={{display:'block'}}
                                  name="generatedId"
                                  value={data.generatedId}
                                  type="number"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                                  <label>startDate</label>
                                  <input
                                  style={{display:'block'}}
                                  className="mt-2"
                                  name="startDate"
                                  value={data.startDate}
                                  type="date"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                                     />
                                  <label>endDate</label>
                                  <input
                                  style={{display:'block'}}
                                  className="mt-2"
                                  name="endDate"
                                  value={data.endDate}
                                  type="date"
                                  required
                                  onChange = { (e) => handelSubmet(e)}
                                          
                                  />
                                   <label>projects</label>
                                   <input
                                       name="projects"
                                       value={data.projects}
                                       className="mt-1"
                                       type="text"
                                       onChange = { (e) => handelSubmet(e)}
                                  />
                                   <label>hardSkills</label>
                                   <input
                                       name="hardSkills"
                                       value={data.hardSkills}
                                       className="mt-1"
                                       type="text"
                                      //  required
                                       onChange = { (e) => handelSubmet(e)}
                                                          /> 
                                   <label>softSkills</label>
                                   <input
                                       name="softSkills"
                                       value={data.softSkills}
                                       className="mt-1"
                                       type="text"
                                      //  required
                                       onChange = { (e) => handelSubmet(e)}
                                                          /> 
                                  <Row>
                                     <div className="col-sm-3 w-25">
                                     <label className="mb-3 mt-3 " >isIntern</label>
                                     </div>
                                     <div className="col-sm-3 w-25 mt-2">
                                     <Switch
                                    color="default"
                                    value={data.isEmployee}
                                    inputProps={{ 'aria-label': 'checkbox with default color' }}
                                    onChange = { (e) => {
                                        setData(prevState =>({...prevState, isEmployee : e.target.checked, isIntern : !e.target.checked}))
                                        
                                    }}
                                  />
                                     </div>
                                     <div className="col-sm-3 w-25">
                                     <label className="mb-3 mt-3">isEmployee</label>
                                     </div>
                                 </Row>
                                  <input type="submit" value="save" onClick={(e)=>edit(e)}/>
                          </Row>
                      </Fragment>
                  </div>
              </Card>
          </div>
      </Row> 
      
    </>
    )
}